import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
// import actions from './action'
import apiService from './apiService'
// console.log(apiService.actions);
import getters from './getters'

Vue.use(Vuex)

const state = {
    login: {},  //
    loading: false, //
    preventLoading: false,  //
    pageLoading: false, //
    login: false, //
    headTopTitle: null,  //
    showHeadTopLogin: false, //
    showHeadTop: true, //
    showHeadTopBack: false, //
    showHeadTopSearch: true, //
    showFootNav: true,  //
    category: [],  //
    brand: [],   //
    cart: {list: {}, num: 0},  //
    addressList: [], //
    coupon: {}, //
}

export default new Vuex.Store({
    state,
    getters,
    // actions,
    mutations,
    // apiService,
    modules: {
        apiService,
    }
})


